
.button__box_technic{
    width: 220px;
    height: 72px;
    white-space: nowrap;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.swiper-container+.logo__repair{
    background-color: #FFFFFF;
}
.button__box_technic{
    background-color:white;
    padding-left: 5px;
}

.technic__button-flex{
    width: 90px;
    height: 24px;
    outline: none;
    border: 0;
    background: transparent;
}

.button__round{
    width: 40px;
    height: 40px;
    outline: none;
    border: 0;
    background: transparent;
}

.button__technic-next{
    background-image: url("data:image/svg+xml;base64,");
    width: 40px;
    height: 40px;
    padding-right: 15px;
    background-repeat: no-repeat;
    transform: translate(-20px, 45px);
}

.text__technic{
    border: 1px solid #EAEAEA ;
    border-radius: 5px;
    margin-right: 20px;
    overflow: hidden;
    margin-top: 10px;
}
.text__brand-repair{
    display: flex;
    padding: 15px;
    width: 95%;
    background-color: white;
    box-shadow: inset 0 1px 0 1px #D9FFF5;
    text-transform: uppercase;
    color: #7E7E82;
}

.extra{
    display: none;
}
.technic__button-flex:before{
    content: '';
    display: flex;
    background-image: url("data:image/svg+xml;base64,");
    width: 8px;
    height: 11px;
}
.technic__button-flex.rotated:before{
    transform: rotate(180deg);
}
.button__text-technic{
  transform: translate(6px, -45px);
}
.repair__equipment{
  padding-top: 10px;
}

@media (min-width: 320px) {


    .button__box_technic{
        width: 240px;
        height: 150px;
    }
    .extra{
        display: flex;
    }

    .technic__button-flex{
        display: none;
    }
    .swiper__technic{
        width: auto;
        height: 200px;
    }

}
@media (min-width: 768px) {
    .swiper{
        width: 100%;
        height: 100%;
    }
    .extra{
        display: none;
    }

    .menu__technic.swiper-wrapper{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        justify-items: center;
        margin-top: 10px;
    }

    .text__technic.swiper-slide{
        width: 240px;
        height: 150px;
        white-space: nowrap;
    }
    .technic__button-flex{
        display: flex;
        justify-content: center;
        padding-top: 10px;
        padding-left: 10px;
    }

}

@media (min-width: 1120px) {


    .menu__technic.swiper-wrapper{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        justify-items: center;
    }
    .extra{
        display: none;
    }

}

